'use client';

import Link from 'next/link';
import { motion } from 'framer-motion';
import { Calendar, Clock, ArrowRight } from 'lucide-react';
import { PostData } from '@/lib/markdown';

interface BlogCardProps {
  post: PostData;
  index: number;
}

export default function BlogCard({ post, index }: BlogCardProps) {
  const readingTime = Math.ceil((post.contentHtml?.length || 0) / 1000);

  return (
    <motion.article
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: index * 0.1 }}
      whileHover={{ y: -5 }}
      className="group"
    >
      <Link href={`/blog/${post.id}`}>
        <div className="relative p-6 bg-[var(--card)] border border-[var(--border)] rounded-lg shadow-sm hover:shadow-lg transition-all duration-300 h-full">
          <div className="absolute inset-0 bg-gradient-to-r from-[var(--primary)] to-blue-400 opacity-0 group-hover:opacity-10 rounded-lg transition-opacity duration-300" />
          
          <div className="relative z-10">
            <div className="flex items-center gap-4 text-sm text-[var(--muted-foreground)] mb-3">
              <span className="flex items-center gap-1">
                <Calendar className="w-4 h-4" />
                {new Date(post.date).toLocaleDateString('en-US', {
                  year: 'numeric',
                  month: 'short',
                  day: 'numeric',
                })}
              </span>
              <span className="flex items-center gap-1">
                <Clock className="w-4 h-4" />
                {readingTime} min read
              </span>
            </div>

            <h2 className="text-xl font-bold mb-2 group-hover:text-[var(--primary)] transition-colors">
              {post.title}
            </h2>

            {post.excerpt && (
              <p className="text-[var(--muted-foreground)] mb-4 line-clamp-3">
                {post.excerpt}
              </p>
            )}

            {post.tags && post.tags.length > 0 && (
              <div className="flex flex-wrap gap-2 mb-4">
                {post.tags.map((tag) => (
                  <span
                    key={tag}
                    className="px-3 py-1 text-xs font-medium bg-[var(--secondary)] text-[var(--secondary-foreground)] rounded-full"
                  >
                    {tag}
                  </span>
                ))}
              </div>
            )}

            <div className="flex items-center text-[var(--primary)] font-medium">
              <span>Read more</span>
              <ArrowRight className="w-4 h-4 ml-1 group-hover:translate-x-1 transition-transform" />
            </div>
          </div>
        </div>
      </Link>
    </motion.article>
  );
}